<template>
  <v-container>
    <v-card>
      <v-tabs v-model="tab" align-with-title>
        <v-tabs-slider color="yellow"></v-tabs-slider>

        <v-tab> Profile </v-tab>
        <v-tab> Application </v-tab>
      </v-tabs>

      <v-tabs-items v-model="tab">
        <v-tab-item>
          <v-card class="mx-auto py-1 mb-2" max-width="auto" flat>
            <v-toolbar-title class="text-h5 ma-4"
              >Solid Waste Management Profile
            </v-toolbar-title>
            <template v-if="this.access_level == '29'">
              <v-row>
                <v-col class="ml-4" cols="3">
                  <v-combobox
                    :items="allRegions"
                    label="Region"
                    v-model="selectRegion"
                    dense
                    outlined
                    @change="
                      getProvince(selectRegion.value);
                      fetchWacsProfile({
                        region_id: selectRegion.value,
                        prov_id: selectProv.value,
                        city_id: selectCity.value,
                      }).then(() => {
                        setWacsProfile();
                      });
                    "
                  ></v-combobox>
                </v-col>
                <v-col cols="3">
                  <v-combobox
                    :items="allProvinces"
                    label="Province"
                    v-model="selectProv"
                    @change="
                      getCity();
                      fetchWacsProfile({
                        region_id: selectRegion.value,
                        prov_id: selectProv.value,
                        city_id: selectCity.value,
                      }).then(() => {
                        setWacsProfile();
                      });
                    "
                    outlined
                    dense
                  ></v-combobox>
                </v-col>
                <v-col cols="3">
                  <v-combobox
                    :items="allCities"
                    label="City"
                    v-model="selectCity"
                    @change="
                      fetchWacsProfile({
                        region_id: selectRegion.value,
                        prov_id: selectProv.value,
                        city_id: selectCity.value,
                      }).then(() => {
                        setWacsProfile();
                      })
                    "
                    outlined
                    dense
                  ></v-combobox>
                </v-col>
                <v-col cols="2">
                  <v-combobox
                    :items="years()"
                    label="Year"
                    v-model="selectYear"
                    @change="
                      fetchWacsProfile({
                        region_id: selectRegion.value,
                        prov_id: selectProv.value,
                        city_id: selectCity.value,
                        year: selectYear.value,
                      }).then(() => {
                        setWacsProfile();
                      })
                    "
                    outlined
                    dense
                  ></v-combobox>
                </v-col>
              </v-row>
            </template>
            <template>
              <v-row>
                <v-col class="text-subtitle-1 ml-4" cols="5">
                  <p class="font-weight-bold mb-1">
                    Solid Waste Management Plan Status
                  </p>
                  <p class="font-weight-bold mb-1">Waste Generated per day</p>
                  <p class="font-weight-bold mb-1">
                    Waste Generation per capita per day
                  </p>
                </v-col>
                <v-col class="text-subtitle-1" cols="6">
                  <p class="mb-1">Not Set</p>
                  <p class="mb-1">{{ this.totalKgPerDay }}</p>
                  <p class="mb-1">Not Set</p>
                </v-col>
              </v-row>
              <v-divider></v-divider>
              <v-row class="mt-1">
                <v-col class="text-subtitle-1 ml-4" cols="4">
                  <p class="font-weight-bold light-blue lighten-1 pl-2">
                    Biodegradable wastes
                  </p>
                  <p class="font-weight-bold green lighten-1 pl-2">
                    Recyclable wastes
                  </p>

                  <p class="font-weight-bold amber lighten-1 pl-2">RWPD</p>
                  <p class="font-weight-bold deep-purple lighten-1 pl-2">
                    Residual wastes for Disposal
                  </p>
                  <p class="font-weight-bold red lighten-1 pl-2">
                    Special wastes
                  </p>
                </v-col>

                <v-col class="text-subtitle-1" cols="2">
                  <p>{{ this.totalBio }}</p>
                  <p>{{ this.totalRec }}</p>
                  <p>{{ this.totalRwpd }}</p>
                  <p>{{ this.totalDis }}</p>
                  <p>{{ this.totalSpec }}</p>
                </v-col>
                <v-col class="text-subtitle-1" cols="5">
                  <apexchart
                    width="450"
                    height="250"
                    type="pie"
                    :options="chartOptions"
                    :series="series"
                  ></apexchart>
                </v-col>
              </v-row>
              <v-divider></v-divider>
              <v-row class="mt-1">
                <v-col class="text-subtitle-1 ml-4" cols="5">
                  <p class="font-weight-bold mb-1">
                    Waste Diversion per day (percentage)
                  </p>
                  <p class="font-weight-bold mb-1">
                    Number of Barangays served by MRFs (percentage)
                  </p>
                  <p class="font-weight-bold mb-1">Final Disposal Site</p>
                  <p class="font-weight-bold mb-1">SWM Organization</p>
                  <p class="font-weight-bold mb-1">Number of Staff</p>
                  <p class="font-weight-bold mb-1">
                    Garbage Haulers/Collection
                  </p>
                  <p class="font-weight-bold mb-1">
                    Annual Budget of the LGU for SWM
                  </p>
                </v-col>
                <v-col class="text-subtitle-1" cols="6">
                  <p class="mb-1">
                    {{ this.divertedKgPerDay }}({{ this.divertedKgPerDayPer }})
                  </p>
                  <p class="mb-1">Not Set</p>
                  <p class="mb-1">Not Set</p>
                  <p class="mb-1">Not Set</p>
                  <p class="mb-1">Not Set</p>
                  <p class="mb-1">Not Set</p>
                  <p class="mb-1">Not Set</p>
                </v-col>
              </v-row>
            </template>
            <!-- <v-card-actions>
        <v-btn color="blue" text> Continue </v-btn>

        <v-btn color="orange" text> Save&Exit </v-btn>
      </v-card-actions> -->
          </v-card>
        </v-tab-item>
        <v-tab-item>
          <v-card class="mx-auto py-1 mt-2" max-width="auto" flat>
            <v-toolbar-title class="text-h5 ma-4"
              >10 Year Application
            </v-toolbar-title>
            <template>
              <v-simple-table>
                <template v-slot:default>
                  <thead>
                    <tr>
                      <th class="text-left">Reference Number</th>
                      <th class="text-left">Date created</th>
                      <th class="text-left">Status</th>
                      <th class="text-left">Year</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="item in desserts" :key="item.name">
                      <td>{{ item.refNumber }}</td>
                      <td>{{ item.dateCreated }}</td>
                      <td>{{ item.status }}</td>
                      <td>{{ item.year }}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </template>
          </v-card>
        </v-tab-item>
      </v-tabs-items>
    </v-card>
  </v-container>
</template>
    </v-card>
  </v-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import Vue from "vue";
import VueApexCharts from "vue-apexcharts";
Vue.use(VueApexCharts);
Vue.component("apexchart", VueApexCharts);
export default {
  // components: {
  //   'apexchart', VueApexCharts
  // },
  name: "HelloWorld",

  data() {
    return {
      chartOptions: {
        labels: [
          "Biodegradable wastes",
          "Recyclable wastes",
          "RWPD",
          "Residual wastes for Disposal",
          "Special wastes",
        ],
      },
      series: [],
      desserts: [
        {
          refNumber: "TryAnda4",
          dateCreated: "08-11-2022",
          status: "Draft",
          year: "2021-2030",
        },
        {
          refNumber: "TryAnda5",
          dateCreated: "08-11-2022",
          status: "Draft",
          year: "2021-2030",
        },
      ],
      tab: null,
      user_region: "",
      selectRegion: [],
      selectProv: [],
      selectCity: [],
      selectYear: [],
      access_level: "",
      totalBio: "No Data",
      totalRec: "No Data",
      totalSpec: "No Data",
      totalRwpd: "No Data",
      totalDis: "No Data",
      totalKgPerDay: "No Data",
      divertedKgPerDay: "No Data",
      divertedKgPerDayPer: "",
    };
  },
  created() {
    this.access_level = sessionStorage.getItem("access");
    this.fetchRegions();

    if (this.access_level != "29") {
      this.fetchWacsProfile({
        region: sessionStorage.getItem("cregion"),
        province: "",
        city: sessionStorage.getItem("city"),
      }).then(() => {
        //this.setChapterDetails();
      });
    }
    // if (this.user_region != "CO") {
    //   this.getProvince(this.user_region);
    // }
  },
  methods: {
    ...mapActions([
      "fetchRegions",
      "fetchProvince",
      "fetchCities",
      "fetchWacsProfile",
    ]),
    getProvince(region) {
      this.selectProv = [];
      this.selectCity = [];

      if (region) {
        this.fetchProvince(region);
      }
    },
    getCity() {
      this.selectCity = [];
      if (this.selectProv) {
        this.fetchCities(this.selectProv.value);
      }
    },
    setWacsProfile() {
      this.series = [];
      let wacsProf = JSON.stringify(this.wacsProf);
      let new_data = JSON.parse(wacsProf)[0];
      // this.totalBio = this.numberWithCommas(
      //   parseFloat(new_data.bio_kg).toFixed(2)
      // );
      // this.totalRec = this.numberWithCommas(
      //   parseFloat(new_data.rec_kg).toFixed(2)
      // );
      // this.totalSpec = this.numberWithCommas(
      //   parseFloat(new_data.special_kg).toFixed(2)
      // );
      // this.totalRwpd = this.numberWithCommas(
      //   parseFloat(new_data.rwpd_kg).toFixed(2)
      // );
      // this.totalDis = this.numberWithCommas(
      //   parseFloat(new_data.disposal_kg).toFixed(2)
      // );
      let totalKg =
        +new_data.totalBio +
        +new_data.totalRec +
        +new_data.totalSpec +
        +new_data.totalRwpd +
        +new_data.totalDis;

      let totalDivertedKg =
        +new_data.totalBio + +new_data.totalRec + +new_data.totalRwpd;
      for (const key in new_data) {
        const value = new_data[key];
        this[key] = this.numberWithCommas(parseFloat(value).toFixed(2));
        let per = ((+value / totalKg) * 100).toFixed(2);
        if (totalKg != 0) {
          this.series.push(parseFloat(per));
        }
      }

      this.totalKgPerDay = this.numberWithCommas(
        parseFloat(totalKg).toFixed(2)
      );

      this.divertedKgPerDay = this.numberWithCommas(
        parseFloat(totalDivertedKg).toFixed(2)
      );

      this.divertedKgPerDayPer = ((totalDivertedKg / totalKg) * 100).toFixed(2);
    },

    numberWithCommas(x) {
      if (x != "NaN" && x != 0) {
        var parts = x.toString().split(".");
        parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
        return parts.join(".") + " kg/day";
      } else {
        return "No Data";
      }
    },
    years() {
      let year = new Date().getFullYear();
      return Array.from(
        { length: 25 },
        (value, index) => parseInt(year) - index
      );
      // console.log(
      //   Array.from({ length: 10 }, (value, index) => parseInt(year) + index)
      // );
    },
  },

  computed: {
    ...mapGetters(["allRegions", "allProvinces", "allCities", "wacsProf"]),
  },
};
</script>
