import con from './config'
import swal from 'sweetalert'
const state = {
  PlanList: [],
  currentRowNo: 0,
  Regions: [],
  Provinces: [],
  Cities: [],
  Chapter: [],
  Attachments: [],
  regionId: 0,
  WacsProf: [],
  // Barangay:[],
  // StartYear:"",
  // Header:[]
}

const getters = {
  allPlanList: state => state.PlanList,
  tgetCurrentrow: state => state.currentRowNo,
  allRegions: state => state.Regions,
  allProvinces: state => state.Provinces,
  allCities: state => state.Cities,
  allChapter: state => state.Chapter,
  allAttach: state => state.Attachments,
  regionId: state => state.regionId,
  wacsProf: state => state.WacsProf,
  // allBarangay: state=>state.Barangay,
  // getStartingYear: state=>state.StartYear,
  // getHeader: state=>state.Header
}

const actions = {

  async getRegionInfo({ commit }, id) {
    const region = sessionStorage.getItem('cregion');
    await con.get(`/GetRegionInfo/${region}`).then(function (res) {
      commit('getregionId', res.data.data[0]['rgnid']);
    }).catch(error => {
    })

  },

  async fetchPlanList({ commit }, row) {
    await con.get(`/PlanList/${row.limit}/${row.offset}/${state.regionId}/${row.status}`).then(function (res) {
      if (res.data['PlanList'].length > 0) {
        commit('getPlanList', res.data['PlanList']);
      } else {
        var rowoffset = row.offset - row.limit;
        if (rowoffset <= 0) {
          rowoffset = 0;
        }
        commit('currentRow', rowoffset);
      }

    }).catch(error => {
      console.log(error);
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
    })
  },
  async increment({ commit }, row) {
    var limitoffset = parseInt(row.offset) + parseInt(row.limit);
    commit('currentRow', limitoffset);
  },
  async decrement({ commit }, row) {
    // decrement
    var limitoffset = parseInt(row.offset) - parseInt(row.limit);
    commit('currentRow', limitoffset);
    if (row.offset <= 0) {
      // commit to 0 if data is less than zero
      commit('currentRow', 0);
    }
  },
  async searchPlanList({ commit }, res) {
    var limit = parseInt(res.limit);
    await con.post('/SearchPlanList', {
      txtsearch: res.txtsearch,
      txtlimit: limit,
      region: state.regionId,
      status: res.status
    }).then(function (res) {
      commit('currentRow', res.data['offset']);
      commit('getPlanList', res.data['PlanList']);
    }).catch(error => {
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
    })
  },
  async deletePlantList({ commit }, res) {
    const region = sessionStorage.getItem('cregion');
    const result = await con.delete(`/PlanList/${res.limit}/${res.offset}/${res.id}/${region}`).catch(error => {
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
    })
    commit('getPlanList', result.data['PlanList']);
  },

  async fetchRegions({ commit }) {
    await con.get(`/GetRegions`).then(function (res) {
      commit('getRegions', res.data['regions']);
    }).catch(error => {
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
    })
  },

  async fetchProvince({ commit }, id) {
    if (sessionStorage.getItem("cregion") != "CO") {
      await con.get(`/GetRegionInfo/${id}`).then(function (res) {
        let id2 = res.data.data[0]['rgnid'];
        con.get(`/GetProvinces/${id2}`).then(function (res) {
          commit('getProvince', res.data['provinces']);
        }).catch(error => {
          if (error.response.data.error == "Expired token") {
            sessionStorage.clear();
            window.location.href = "https://iis.emb.gov.ph/embis/";
          }
        })

      }).catch(error => {

      })

    } else {
      con.get(`/GetProvinces/${id}`).then(function (res) {
        commit('getProvince', res.data['provinces']);
      }).catch(error => {
        if (error.response.data.error == "Expired token") {
          sessionStorage.clear();
          window.location.href = "https://iis.emb.gov.ph/embis/";
        }
      })

    }

  },

  async fetchCities({ commit }, id) {
    await con.get(`/GetCities/${id}`).then(function (res) {
      commit('getCities', res.data['cities']);
    }).catch(error => {
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
    })
  },

  async CreateHeaderPlan({ commit }, res) {
    var year = res.year;
    const region = sessionStorage.getItem('cregion');

    await con.post(`/PlanList/${res.limit}/${res.offset}/${region}`,
      {
        region_id: res.region,
        province_id: res.province,
        city_id: res.city,
        reference_number: res.referenceNumber,
        year: year,
        receiver: res.receiver,
        remarks: res.remarks,
        pcg: res.pcg,
        waste_kg_day: res.waste_kg_day.replace(/\,/g, ''),
      }).then(function (res) {
        commit('getPlanList', res.data['PlantList']);
        swal({
          title: "Chapter 1",
          text: "Continue ?, Please save your work before leaving the work station.",
          icon: "success",
          buttons: true,
          buttons: true,
        })
          .then((willContinue) => {
            if (willContinue) {
              // commit('startingYear', year);
              // commit('getBarangay', res.data['barangay']);
              // commit('header', res.data['header']);
              sessionStorage.setItem("city_id", res.data['header']['city_id']);
              sessionStorage.setItem("header_id", res.data['header']['id']);
              sessionStorage.setItem("startingYear", res.data['header']['year']);
              commit("getChapter", JSON.stringify(null));
              // window.location.href = "#/plan";
              window.location.href = "#/plan?city=" + res.data['header']['city_id'] + "&header_id=" + res.data['header']['id'] + "&chapter=" + 1;

            }
          });
      }).catch(error => {
        if (error.response.data.error == "Expired token") {
          //sessionStorage.clear();
          window.location.href = "https://iis.emb.gov.ph/embis/";
        } else {
          var errorLog = JSON.stringify(error.response.data);
          swal({
            title: "Server Side Validation",
            text: errorLog,
            icon: "error",
            dangerMode: true,
          });
        }
      })


  },
  async UpdateHeaderPlan({ commit }, res) {
    var year = res.year;
    const region = sessionStorage.getItem('cregion');

    await con.post(`/UpdatePlanHeader/${res.limit}/${res.offset}/${region}`,
      {
        id: res.id,
        // region_id: res.region,
        // province_id: res.province,
        // city_id: res.city,
        // reference_number: res.referenceNumber,
        year: year,
        receiver: res.receiver,
        remarks: res.remarks,
        pcg: res.pcg,
        waste_kg_day: res.waste_kg_day.replace(/\,/g, ''),
      }).then(function (res) {
        commit('getPlanList', res.data['PlantList']);
        swal({
          title: "Update Successfully",
          icon: "success",
        })
      }).catch(error => {
        if (error.response.data.error == "Expired token") {
          //sessionStorage.clear();
          window.location.href = "https://iis.emb.gov.ph/embis/";
        } else {
          var errorLog = JSON.stringify(error.response.data);
          swal({
            title: "Server Side Validation",
            text: errorLog,
            icon: "error",
            dangerMode: true,
          });
        }
      })


  },

  async fetchPerChapter({ commit }, res) {

    sessionStorage.setItem("city_id", res.city);
    sessionStorage.setItem("header_id", res.header_id);
    var chapter = res.chapter;
    var view = res.view;
    await con.get(`/GetChapter/${res.header_id}/${chapter}`
    ).then(function (res) {
      commit("chapter", chapter);
      // if (chapter == 13) {
      //   let [b] = [res.data['Chapter']].map((e, i) => ({

      //     ...e, ten_year_plan: JSON.parse(e.ten_year_plan).map((e, i) => (

      //       { ...e, new_name: `http://localhost/swms_api/uploads/${e.new_name}` }

      //     )), others: JSON.parse(e.others).map((e, i) => (

      //       { ...e, new_name: `http://localhost/swms_api/uploads/${e.new_name}` }

      //     ))
      //   }

      //   ))
      //   commit("getChapter", b);
      // } else {
      commit("getChapter", res.data['Chapter']);
      //}

      if (view == undefined) {
        window.location.href = "#/plan?city=" + sessionStorage.getItem("city_id") + "&header_id=" + sessionStorage.getItem("header_id") + "&chapter=" + chapter;
      } else {

        window.location.href = "#/plan?city=" + sessionStorage.getItem("city_id") + "&header_id=" + sessionStorage.getItem("header_id") + "&chapter=" + chapter + "&view=" + view;
      }

      // this.$router.push({
      //   path: "plan",
      //   query: {
      //     chapter: chapter,
      //   },
      // });
    }).catch(error => {
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
      else if (error.response.data.error == "Key may not be empty") {
        sessionStorage.clear();
        window.location.href = "#/eplan";
      } else {
        var errorLog = JSON.stringify(error.response.data);
        swal({
          title: "Server Side Validation",
          text: errorLog,
          icon: "error",
          dangerMode: true,
        }).then((willContinue) => {
          if (willContinue) {
            commit("chapter", chapter);
          }
        });
      }
    })
  },

  async fetchAttachPerChapter({ commit }, res) {

    sessionStorage.setItem("city_id", res.city);
    sessionStorage.setItem("header_id", res.header_id);
    var chapter = res.chapter;
    await con.get(`/GetChapterAttach/${res.header_id}/${chapter}`
    ).then(function (res) {
      commit("chapter", chapter);
      commit("getAttach", res.data['Result']);

      //window.location.href = "#/plan?city=" + sessionStorage.getItem("city_id") + "&header_id=" + sessionStorage.getItem("header_id") + "&chapter=" + chapter;
    }).catch(error => {
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
      else if (error.response.data.error == "Key may not be empty") {
        sessionStorage.clear();
        window.location.href = "#/eplan";
      } else {
        var errorLog = JSON.stringify(error.response.data);
        swal({
          title: "Server Side Validation",
          text: errorLog,
          icon: "error",
          dangerMode: true,
        }).then((willContinue) => {
          if (willContinue) {
            commit("chapter", chapter);
          }
        });
      }
    })
  },

  async fetchWacsProfile({ commit }, res) {

    // let linkprov, linkcity = "";
    // if (res.prov_id != undefined) {
    //   linkprov = "/${res.prov_id}"
    // }
    // if (res.city_id != undefined) {
    //   linkcity = "/${res.city_id}"
    // }
    console.log(res.year)
    await con.get(`/GetWAcsProfile/${res.region_id}/${res.prov_id}/${res.city_id}`
    ).then(function (res) {
      commit("getwacsProf", res.data['data']);
    }).catch(error => {
      if (error.response.data.error == "Expired token") {
        sessionStorage.clear();
        window.location.href = "https://iis.emb.gov.ph/embis/";
      }
    })
  },






};

const mutations = {
  getPlanList: (state, PlanList) => (state.PlanList = PlanList),
  currentRow: (state, Irow) => (state.currentRowNo = Irow),
  getRegions: (state, RegionList) => (state.Regions = RegionList),
  getProvince: (state, ProvinceList) => (state.Provinces = ProvinceList),
  getCities: (state, CityList) => (state.Cities = CityList),
  getChapter: (state, c) => (state.Chapter = c),
  getAttach: (state, c) => (state.Attachments = c),
  getregionId: (state, regionIdres) => (state.regionId = regionIdres),
  getwacsProf: (state, wacsProfres) => (state.WacsProf = wacsProfres),
  // getBarangay: (state, BarangayList) => (state.Barangay = BarangayList),
  // startingYear: (state, StartYear) => (state.StartYear = StartYear),
  // header: (state, Header) => (state.Header = Header),
}

export default {
  state,
  getters,
  actions,
  mutations
};