<template>
  <v-app>
    <v-navigation-drawer v-model="drawer" color="#08507e" app id="sidebar">
      <v-list-item>
        <v-list-item-avatar>
          <v-img src="favicon.ico"></v-img>
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-title class="title white--text">
            Welcome
          </v-list-item-title>
          <v-list-item-subtitle class="white--text">
            {{ user_name }}
          </v-list-item-subtitle>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <v-list dense nav>
        <template v-for="item in newAllLink()">
          <v-list-item
            v-if="item.children == 0"
            :key="item.title"
            :to="item.to"
            link
          >
            <v-list-item-icon>
              <v-icon color="white">{{ item.icon }}</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title class="white--text">{{
                item.title
              }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-group
            :key="item.title"
            v-else
            :group="item.path"
            color="indigo"
          >
            <!-- this template is for the title of top-level items with children -->
            <template #activator>
              <v-list-item-icon>
                <v-icon color="white">{{ item.icon }}</v-icon>
              </v-list-item-icon>

              <v-list-item-content>
                <v-list-item-title class="white--text">{{
                  item.title
                }}</v-list-item-title>
              </v-list-item-content>
            </template>
            <!-- this template is for the children/sub-items (2nd level) -->
            <template v-for="subItem in item.childrens">
              <v-list-item class="ml-5" :key="subItem.title" :to="subItem.path">
                <v-list-item-icon class="mr-4">
                  <v-icon color="white">{{ subItem.icon }}</v-icon>
                </v-list-item-icon>
                <v-list-item-title class="white--text">
                  {{ subItem.title }}
                </v-list-item-title>
              </v-list-item>
            </template>
          </v-list-group>
        </template>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar app id="header">
      <v-app-bar-nav-icon
        @click="drawer = !drawer"
        color="black"
      ></v-app-bar-nav-icon>

      <v-toolbar-title class="black--text"
        >SOLID WASTE MANAGEMENT SYSTEM</v-toolbar-title
      >
      <!-- <input v-model="userToken" type="text" /> -->
      <v-spacer></v-spacer>
      <v-btn icon>
        <v-icon @click="logout">mdi-logout-variant</v-icon>
      </v-btn>
    </v-app-bar>

    <v-main>
      <!--  -->
      <router-view></router-view>
    </v-main>
    <v-footer padless>
      <v-col class="text-center" cols="12">
        {{ new Date().getFullYear() }}  <strong>SWMS</strong>
      </v-col>
    </v-footer>
  </v-app>
</template>

<script>
var url = window.location;

let searchParams = new URLSearchParams(url.search);
let iisToken = searchParams.get("token");
let iisTokenID = searchParams.get("token_id");
let origin = searchParams.get("origin");

import { mapActions, mapGetters } from "vuex";
import swal from "sweetalert";
export default {
  data: () => ({
    token: iisToken,
    token_id: iisTokenID,
    origin: origin,
    drawer: null,
    user_name: "",
  }),
  methods: {
    ...mapActions(["getIISToken", "getSidebar", "getSubSidebar"]),
    logout() {
      swal({
        text: "Are you sure to end your session?",
        icon: "warning",
        buttons: true,
        dangerMode: true,
      }).then((willEnd) => {
        if (willEnd) {
          sessionStorage.clear();
          window.location.href = "https://iis.emb.gov.ph/embis/";
        }
      });
    },
    newAllLink() {
      let item = this.allLinks.map((e, i) => {
        let x = this.allSubLinks.filter((el, il) => e.to == el.header_path);
        return { ...e, childrens: x };
      });
      return item;
    },
  },
  computed: mapGetters(["allLinks", "allSubLinks"]),

  created() {
    this.getIISToken({
      token: this.token,
      id: this.token_id,
      origin: this.origin,
    }).then(() => {});

    this.getSidebar({
      access_level: sessionStorage.getItem("access"),
    }).then(() => {});
    this.getSubSidebar({
      access_level: sessionStorage.getItem("access"),
    });

    this.user_name = sessionStorage.getItem("name");
  },
};
</script>